import { Vue } from 'vue-class-component'
import { createRouter, createWebHashHistory, RouteRecordRaw } from 'vue-router'
import HomeView from '../views/Home.vue'
import AddPage from '../views/AddItem.vue'
import EditPage from '../views/EditItem.vue'
import RecordPage from '../views/Record.vue'
import HistoryPage from '../views/History.vue'
import NotFoundPage from '../views/NotFound.vue'
const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
    meta: {
      title: 'Home'
    }
  },
  {
    path: '/add',
    name: 'AddPage',
    component: AddPage,
    meta: {
      title: 'Add Items'
    }
  },
  {
    path: '/edit/:id/:name/:sale_price/:original_price',
    name: 'EditPage',
    component: EditPage,
    meta: {
      title: 'Edit Items'
    }
  },
  {
    path: '/record',
    name: 'RecordPage',
    component: RecordPage,
    meta: {
      title: 'Record Lists'
    }
  },
  {
    path: '/history',
    name: 'HistoryPage',
    component: HistoryPage,
    meta: {
      title: 'Monthly Lists'
    }
  },
  {
    path: '/:pathMatch(.*)*',
    name: 'NotFoundPage',
    component: NotFoundPage,
  },
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

router.beforeEach((to, from, next) => {
  document.title = `${to.meta.title ? to.meta.title : 'not found'} | 4BROS`;
  next()
})
export default router
